import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from 'src/app/shared/shared.module';

import { RouterModule } from '@angular/router';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatMenuModule } from '@angular/material/menu';
import { MatRadioModule } from '@angular/material/radio';
import { FullCalendarModule } from '@fullcalendar/angular';
import { CondoDateRangeModule } from 'src/@condo/components/date-range/date-range.module';
import { CalendarComponent } from 'src/app/modules/common/calendar/calendar.component';
import { CalendarRecurrenceComponent } from 'src/app/modules/common/calendar/recurrence/recurrence.component';
import { CalendarSettingsComponent } from 'src/app/modules/common/calendar/settings/settings.component';
import { CalendarSidebarComponent } from 'src/app/modules/common/calendar/sidebar/sidebar.component';
import { calendarRoutes } from './calendar.routing';

@NgModule({
    declarations   : [
        CalendarComponent,
        CalendarRecurrenceComponent,
        CalendarSettingsComponent,
        CalendarSidebarComponent
    ],
    entryComponents: [
        CalendarRecurrenceComponent
    ],
    imports: [
        CommonModule,
        SharedModule,
        RouterModule.forChild(calendarRoutes),
        ScrollingModule,
        MatFormFieldModule,
        MatMenuModule,
        MatRadioModule,
        FullCalendarModule,
        CondoDateRangeModule
        
    ],
    providers      : [
        {
            provide : MAT_DATE_FORMATS,
            useValue: {
                parse  : {
                    dateInput: 'DD.MM.YYYY'
                },
                display: {
                    dateInput         : 'DD.MM.YYYY',
                    monthYearLabel    : 'MMM YYYY',
                    dateA11yLabel     : 'DD.MM.YYYY',
                    monthYearA11yLabel: 'MMMM YYYY'
                }
            }
        }
    ]
})
export class CalendarModule
{
}
